.nav-menu .main-menu a {
	font: 15px "Open Sans";
	margin-right: 35px;
	color: #fff;
	position: relative;
	padding: 25px 0px 15px 0px;
	cursor: pointer;
	transition: 0.3s;
}

.nav-menu .main-menu a.v-stroke {
	padding: 25px 0px 30px 0px;
}

.nav-menu .main-menu a.v-stroke:hover::after {
	height: 30px;
	transition: all 0.2s ease-in-out;
}

.nav-menu .main-menu a.v-stroke:before {
	content: '';
	position: absolute;
	top: 55px;
	left: 50%;
	width: 2px;
	height: 0px;
	background: #fff;
	transition: all 0.2s ease-in-out;
}

.nav-menu .main-menu a.v-stroke:hover::before {
	height: 30px;
	transition: all 0.2s ease-in-out;
}

.nav-menu .main-menu a.h-stroke:hover::after {
	width: 100%;
	transition: all 0.2s ease-in-out;
}

.nav-menu .main-menu a.h-stroke:before {
	content: '';
	position: absolute;
	top: 55px;
	bottom: 0;
	right: 0;
	height: 2px;
	width: 0%;
	background: #ffffff;
	transition: all 0.2s ease-in-out;
}

.nav-menu .main-menu a.h-stroke:hover::before {
	width: 100%;
	transition: all 0.2s ease-in-out;
}

